import React from 'react';
import useForm from './form';
import {Link} from 'react-router-dom';

const ResetPassForm = (props) => {
    const stateSchema = {
        password: { value: '', error: '' },
        confirmpassword:{value: '', error: ''}
    };

    const validationStateSchema = {
        password: {
          required: true
        },
        confirmpassword: {
          required: true
        }
      };

      const { state, handleOnChange, handleOnSubmit, disable } = useForm(
        stateSchema,
        validationStateSchema,
        props.method
      );

      return (
        <form id="reset-form" onSubmit={handleOnSubmit} className={props.classes.formreset}>
            <div className="bg-white rounded box-shadow p-5 h-100 justify-content-center align-items-center">
                <div className="text-center">
                    <h1 className="h3 mb-3 font-weight-normal">Reset Password</h1>
                </div>
                <div className="form-group">
                    <label htmlFor="email" className="sr-only">Password</label>
                    <input onChange={handleOnChange} type="password" id="password" className="form-control" name="password" placeholder="Password"/>
                    {state.password.error && <p className="invalid-feedback">{state.password.error}</p>}
                </div>
                <div className="form-group">
                    <label htmlFor="confirmpassword" className="sr-only">Confirm Password</label>
                    <input onChange={handleOnChange} type="password" id="confirmpassword" className="form-control" name="confirmpassword" placeholder="Confirm Password"/>
                    {state.confirmpassword.error && <p className="invalid-feedback">{state.confirmpassword.error}</p>}
                </div>
                <button disabled={props.loading || disable?true:false} className="btn btn-lg btn-primary btn-block" type="submit">Reset Password{props.loading?(<i className="fa fa-circle-o-notch fa-spin ml-3"></i>):null}</button>
                <p className="text-center">
                    <Link className="btn" to="/signin">Back to login</Link>
                </p>
            </div>
         </form>
      );
}

export default ResetPassForm;